import { Injectable } from '@angular/core';
import { Http, RequestOptionsArgs  } from '@angular/http';

import { Metric, MetricQuery, SecondMetricRequest, SecondTrendRequest } from '../model/il-board.metric';
import { PartSalesBarOption,EchartsLineOption,EchartsBarOption } from '../model/il-region-chart.option';
import 'rxjs/add/operator/toPromise';
import { NzNotificationService } from 'ng-zorro-antd';
import { SALES } from '../data/mock-sales-data';
import { IlRegionQuery } from '../model/il-region-query';
@Injectable()
export class PartitionAnalysisService {

    constructor(private http: Http, private _notification: NzNotificationService) { }

    static il_url = {
        lastMonthAreaData: "/app/v1/il/region/lastMonth/area",
        lastMonthWarehouseData: "/app/v1/il/region/lastMonth/warehouse",
        allYearAreaData: "/app/v1/il/region/allYear/area",
        allYearWarehouseData: "/app/v1/il/region/allYear/warehouse",
        allYearTableColumn: "/app/v1/il/region/allYearTableColumn",
        allYearTableModel: "/app/v1/il/region/allYearTableModel",
        lastMonthSalesAnalysis: "/app/v1/il/region/lastMonthSalesAnalysis",
        lastMonthTimeAnalysis: "/app/v1/il/region/lastMonthTimeAnalysis",
        depts: '/app/v1/perm/dept/list'
    };

    deptslist(): Promise<any> {
        return this.http.get(PartitionAnalysisService.il_url.depts)
            .map(res => res.json()).toPromise();
    }

   

    /**
     *
     * @param caliber /
     */
    queryLastMonthAreaList(data: IlRegionQuery): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.lastMonthAreaData, {
                params: data
            }).map(resp => resp.json()).toPromise();

    }

    /**
     *
     * @param caliber /
     */
    queryLastMonthWarehouseList(data: IlRegionQuery): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.lastMonthWarehouseData, {
                params: data
            }).map(resp => resp.json()).toPromise();

    }


    /**
     *
     * @param caliber /
     */
    queryAllYearAreaList(data: IlRegionQuery): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.allYearAreaData, {
                params: data
            }).map(resp => resp.json()).toPromise();

    }
    /**
     *
     * @param caliber /
     */
    queryAllYearWarehouseList(data: IlRegionQuery): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.allYearWarehouseData, {
                params: data
            }).map(resp => resp.json()).toPromise();

    }

    /**
     *
     * @param caliber /Table
     */
    queryAllYearTableColumn(data: IlRegionQuery): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.allYearTableColumn, {
                params: data
            }).map(resp => resp.json()).toPromise();

    }
    /**
     *
     * @param caliber /Table
     */
    queryAllYearTableModel(data: IlRegionQuery): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.allYearTableModel, {
                params: data
            }).map(resp => resp.json()).toPromise();

    }

    /**
     *
     * @param 
     */
    queryLastMonthCaliberOfRegionSales(deptNo:string): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.lastMonthSalesAnalysis+'/'+deptNo)
            .map(resp => resp.json()).toPromise();

    }

     /**
     *
     * @param 
     */
    queryLastMonthRegionTime(deptNo:string): Promise<any> {
        return this.http
            .get( PartitionAnalysisService.il_url.lastMonthTimeAnalysis+'/'+deptNo)
            .map(resp => resp.json()).toPromise();

    }

    buildLastMonthTimeAnalysisBarOption(data: any): EchartsBarOption {
        let title={
            text:"",
            subtext:"",
        }
        return EchartsBarOption.build(title,data.legend,data.xAxis,data.series);
    }

    buildLastMonthSalesAnalysisBarOption(data: any): EchartsBarOption {
        let title={
            text:"",
            subtext:""
        }

        return EchartsBarOption.build(title,data.legend,data.xAxis,data.series);
    }


    buildLastMonthAreaBarOption(data: any,title:string): PartSalesBarOption {

        return PartSalesBarOption.build('',title,data.xAxis,"",data.series);
    }

    buildAllYearLineOption(data: any): EchartsLineOption {
        let title = "";
        return EchartsLineOption.build(title,data.legend,data.xAxis,data.series);
    }
 

    /*utils */
    printErr(error) {
        this._notification.error("", error);
    }

    secmetrics: Metric[] = [
        {key:"1", name:''},
        {key:"2", name:''}];

}



