import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
 
import { PartSaleDistComponent }   from './part-sale-dist/part-sale-dist.component';
import { AreaOrderDataComponent } from './area-order-data/area-order-data.component';
import { AreaSaleDataComponent } from './area-sale-data/area-sale-data.component';
import { PartOrderDistComponent } from './part-order-dist/part-order-dist.component';
import { PartitionAnalysisComponent } from './partition-analysis.component';

const routes: Routes = [
  {
	path: '', component: PartitionAnalysisComponent,
    children: [
		{ path: 'part-sale-dist',  component: PartSaleDistComponent },
		{ path: 'area-order-data',  component: AreaOrderDataComponent },
		{ path: 'area-sale-data',  component: AreaSaleDataComponent },
		{ path: 'part-order-dist',  component: PartOrderDistComponent },
    ]

  }
  
];
 
@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ]
})
export class PartitionAnalysisRoutingModule {}


