import { Component, OnInit } from '@angular/core';
import { PartitionAnalysisService } from '../service/partition-analysis.service';
import { IlRegionQuery } from '../model/il-region-query';
@Component({
  selector: 'app-area-sale-data',
  templateUrl: './area-sale-data.component.html',
  styleUrls: ['./area-sale-data.component.css']
})
export class AreaSaleDataComponent implements OnInit {

   
  expandDataCache = {};

  collapse(array, data, $event) {
    if ($event === false) {
      if (data.children) {
        data.children.forEach(d => {
          const target = array.find(a => a.key === d.key);
          target.expand = false;
          this.collapse(array, target, false);
        });
      } else {
        return;
      }
    }
  }

  convertTreeToList(root) {
    const stack = [], array = [], hashMap = {};
    stack.push({ ...root, level: 0, expand: false });

    while (stack.length !== 0) {
      const node = stack.pop();
      this.visitNode(node, hashMap, array);
      if (node.children) {
        for (let i = node.children.length - 1; i >= 0; i--) {
          stack.push({ ...node.children[ i ], level: node.level + 1, expand: false, parent: node });
        }
      }
    }

    return array;
  }

  visitNode(node, hashMap, array) {
    if (!hashMap[ node.key ]) {
      hashMap[ node.key ] = true;
      array.push(node);
    }
  }

  constructor(private service: PartitionAnalysisService) { }
 
 
  ilRegionQuery = new IlRegionQuery();
  
  areaDataSendList =[];
  areaDataReceiveList = [];
  title ="";
  caliber:number = 1;
  deptNo:string;
  
  setAreaSaleDataToNull(){
    this.areaDataReceiveList = [];
    this.areaDataSendList = [];
  }

  changeSalesTableData(){
    if(this.deptNo){
      if(this.caliber==1){
         this.title ="";
          if(this.areaDataSendList.length==0){
             this.loadTableData();
          }else{
            this.data = this.areaDataSendList;
          this.initForCacheData();
          }
       }else{
         this.title ="";
          if(this.areaDataReceiveList.length==0){
             this.loadTableData();
          }else{
            this.data = this.areaDataReceiveList;
            this.initForCacheData();
          }
       }
    }
  }

  loadTableData(){
  	this.ilRegionQuery.Caliber(this.caliber);
    this.ilRegionQuery.SelectTab(1);
    this.ilRegionQuery.DeptNo(this.deptNo);
     this.service.queryAllYearTableModel(this.ilRegionQuery).then(
        	data => {
        		this.data = data.body;
            this.initForCacheData();
        		if(this.caliber==1){
                 this.areaDataSendList = this.data;

        		}else{
        			this.areaDataReceiveList = this.data;
        		}
        	}
      	);
  }

  initForCacheData(){
    this.data.forEach(item => {
                this.expandDataCache[ item.key ] = this.convertTreeToList(item);
           });
  }

 data = [];
  ngOnInit() {
 /*    this.data.forEach(item => {
      this.expandDataCache[ item.key ] = this.convertTreeToList(item);
      console.log(item.key);
    });  
    this.ilRegionQuery = new IlRegionQuery();
  	this.ilRegionQuery = this.ilRegionQuery.SelectTab(1); 
      this.service.queryAllYearTableModel(this.ilRegionQuery).then(
      	data =>{
           this.data = data.body;
           this.areaDataSendList= this.data; 
           this.initForCacheData();
      	}
      	);*/
  }
 

  	

  

}



