import echarts from 'echarts/dist/echarts.js';
class EchartsOption {
    color?:any;
    title?:any;
    tooltip ?:any;
    legend ?: any;
    toolbox?:any;
    grid?:any;
    calculable ?: boolean;
    xAxis:Array<any>;
    yAxis:Array<any>;
    series:Array<any>;

    constructor() {
        this.calculable = false;
    }
}

export class EchartsLineOption extends EchartsOption {
    
    constructor() {
        super();
        this.toolbox = {
            show : false
        },
        this.yAxis = [{
            type : 'value',
            name :'',
            axisLine: {
              lineStyle: {
                color: '#ccc'
              }
            }
        }];
        this.title = {
            x: 'center',
        };
        this.grid = {
            top: '20%',
            left: '20%',
            right: '10%',
        }
    }

    static build(title:any,legend:any,xAxis: Array<any>,series:Array<any>):EchartsLineOption{
        let echartsLineOption = new EchartsLineOption();
        echartsLineOption.color = ['#7ac6ff', '#9fd96b', '#7c72d8', '#f1d675', '#9fe0f5', '#43b596', '#4b47a6'];
        echartsLineOption.title = title;
        echartsLineOption.tooltip = {
            trigger: 'axis',
            axisPointer: {            // 
              type: 'line'        // 'line' | 'shadow'
            },
            formatter: function (params, ticket, callback) {
              var dataIndex=params[0].dataIndex;
              var res = '<div style="z-index:999">'+params[0].name+'<br/>';
              var count = params.length ;
              for (var i = 0, l = series.length; i < l; i++) {
                  for(var j = 0 ,m = params.length;j<m;j++){
                    if(series[i].name == params[j].seriesName){
                        res +=   series[i].name + ' : ' + series[i].data[dataIndex] + '<br/>';
                    }
                  }
                  
              }
              res +='</div>';
              return res;
            }
          },
        echartsLineOption.legend ={
             itemWidth: 6,
             itemHeight: 6,
             borderRadius: 10,
             data:legend
            //data:[]
        };
        echartsLineOption.xAxis =[{
            type: 'category',
            boundaryGap: false,
            data: xAxis,
            axisLine: {
                lineStyle: {
                    color: '#999'
                }
            },
            axisLabel:{
                rotate: 45
            }
        }];
        echartsLineOption.series  = series;
        return echartsLineOption;
    }

    SetSeries(series: any): EchartsLineOption {
        this.series = series;
        return this;
    }
    SetxAxis(xAxis: any): EchartsLineOption {
        this.xAxis = xAxis;
        return this;
    }
}

export class EchartsBarOption extends EchartsOption {
    
    constructor() {
        super();
        this.tooltip = {
            trigger: 'axis'
        };
        this.toolbox = {
            show : false
        };
        this.yAxis = [{
            type : 'value',
            name:'',
            axisLine: {
              lineStyle: {
                color: '#ccc'
              }
            },
            boundaryGap: ['0%', '20%'],
            axisTick:{
              show:false
            }
        }];
        this.title = {
            x: 'center',
        };
        this.grid = {
            top: '20%',
            left: '20%',
            right: '10%',

        }
    }

    static build(title:any,legend:any,xAxis: Array<any>,series:Array<any>):EchartsBarOption{
        let echartsBarOption = new EchartsBarOption();
        echartsBarOption.color = ['#7ac6ff', '#9fd96b', '#7c72d8', '#f1d675', '#9fe0f5', '#43b596', '#4b47a6'];
        echartsBarOption.title = Object.assign(echartsBarOption.title,title);
        echartsBarOption.xAxis = [{
            type: 'category',
            data: xAxis,
            axisLine: {
                lineStyle: {
                    color: '#999'
                }
            },
            axisLabel:{
                rotate: 45
            }
        }];
        echartsBarOption.series  = series;
        echartsBarOption.legend = {
            data:legend,
            x: 'right',
            itemWidth: 6,
            itemHeight: 6,
        };
        return echartsBarOption;
    }

    SetSeries(series: any): EchartsBarOption {
        this.series = series;
        return this;
    }
    SetxAxis(xAxis: any): EchartsBarOption {
        this.xAxis = xAxis;
        return this;
    }
}

export class PartSalesBarOption extends EchartsOption {
    
    constructor() {
        super();
        this.tooltip = {
            trigger: 'axis'
        } ;
        this.toolbox = {
            show : false
        },
        this.yAxis = [{
            type : 'value',
            axisLine: {
              lineStyle: {
                color: '#ccc'
              }
            },
            axisTick:{
              show:false
            },
            name: '',
        }];
        this.grid = {
            top: '20%',
            left: '20%',
            right: '10%',
            y2:65
        }
    }

    static build(title:string,subTitle:string,xAxisData: Array<string>,
     seriesName:string,seriesData:Array<number>):PartSalesBarOption{
        let barChartOption = new PartSalesBarOption();
        barChartOption.color = ['#7ac6ff', '#9fd96b', '#7c72d8', '#f1d675', '#9fe0f5', '#43b596', '#4b47a6'];
        barChartOption.title = {
              text:title,
              subtext:subTitle,
              x: 'center'
        };
        barChartOption.xAxis =[{
              type:'category',
              data:xAxisData,
              axisLine: {
                    lineStyle: {
                        color: '#999'
                    }
                },
                axisLabel:{
                    interval:0,
                    rotate: 20
                }
        }];
        barChartOption.series = [{
            name:seriesName,
            type:'bar',
            data:seriesData,
            itemStyle: {
                    normal: {
                        barBorderRadius: 7,
                        color: new echarts.graphic.LinearGradient(0,0,0,1,[{
                            offset: 0, color: 'rgba(122,198,255,1)'
                          },{
                            offset: 1, color: 'rgba(122,198,255,0.5)'
                          }],false)
                    }
                },
            barMaxWidth: 14,
        }];
        return barChartOption;
    }

    SetSeries(series: any): PartSalesBarOption {
        this.series = series;
        return this;
    }
    SetxAxis(xAxis: any): PartSalesBarOption {
        this.xAxis = xAxis;
        return this;
    }
}




