import 'rxjs/add/operator/switchMap';
import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { DatePipe } from '@angular/common';

import { routerTransition } from '../../../router.animations';

import { PartitionAnalysisService } from '../service/partition-analysis.service';
import { PartSalesBarOption,EchartsLineOption } from '../model/il-region-chart.option';
import { NzNotificationService } from 'ng-zorro-antd';
import { IlRegionQuery } from '../model/il-region-query';

@Component({
  selector: 'app-part-sale-dist',
  templateUrl: './part-sale-dist.component.html',
  styleUrls: ['./part-sale-dist.component.css'],
  animations: [routerTransition()]
})
export class PartSaleDistComponent implements OnInit {

	constructor(private service: PartitionAnalysisService,private _notification: NzNotificationService) {    }

    ilRegionQuery = new IlRegionQuery();

    BarEchartsOption : PartSalesBarOption;
    AreaSalesBarOption : PartSalesBarOption;
    WarehouseSalesBarOption : PartSalesBarOption;
    AreaSalesBarOutOption : PartSalesBarOption;
    WarehouseSalesBarOutOption : PartSalesBarOption;

    deptNo:string;
    chartInstance = null;
    lineInstance = null;

    currSubTab : number = 1; //1. 2.

    caliber:number  =1 //1. 2.

    title:string = '';

    LineEchartsOption : EchartsLineOption;
    AreaAllSalesLineOption : EchartsLineOption ;
    AreaAllSalesOutLineOption : EchartsLineOption ;
    WarehouseAllSalesLineOption : EchartsLineOption ;
    WarehouseAllSalesOutLineOption : EchartsLineOption ;

    
    setPartSaleDistOptionToNull(){

      this.BarEchartsOption=null;

      this.AreaSalesBarOption = null;
      this.WarehouseSalesBarOption = null;
      this.AreaSalesBarOutOption = null;
      this.WarehouseSalesBarOutOption = null;

      this.AreaAllSalesLineOption = null  ;
      this.AreaAllSalesOutLineOption = null  ;
      this.WarehouseAllSalesLineOption = null  ;
      this.WarehouseAllSalesOutLineOption = null  ;

    }
    
    /**
     * tab
     * @param secondMetric
     */
    onSelectTabChange() {
        if (!this.currSubTab || !(this.currSubTab >= 0 || !this.deptNo)) {
            //eventmetric
            return;
        }
        if(this.currSubTab==1){
            if(this.caliber==1){
                this.fillAreaSalesLineOption(this.WarehouseAllSalesLineOption,this.AreaAllSalesLineOption);
                this.reloadBarChartData(this.AreaSalesBarOption);
                this.reloadLineChartData(this.AreaAllSalesLineOption);
            }else{
                this.fillAreaSalesLineOption(this.WarehouseAllSalesOutLineOption,this.AreaAllSalesOutLineOption);
                this.reloadBarChartData(this.AreaSalesBarOutOption);
                this.reloadLineChartData(this.AreaAllSalesOutLineOption);
            }
        }else{
            if(this.caliber==1){
                this.fillWarehouseSalesLineOption(this.AreaAllSalesLineOption,this.WarehouseAllSalesLineOption);
                this.reloadBarChartData(this.WarehouseSalesBarOption);
                this.reloadLineChartData(this.WarehouseAllSalesLineOption);
            }else{
                this.fillWarehouseSalesLineOption(this.AreaAllSalesOutLineOption,this.WarehouseAllSalesOutLineOption);
                this.reloadBarChartData(this.WarehouseSalesBarOutOption);
                this.reloadLineChartData(this.WarehouseAllSalesOutLineOption);
            }
        }
        
    }

    reloadBarChartData(any: PartSalesBarOption){
       if(!any){
          this.initBarChartData();
       }else{
           this.BarEchartsOption = any;
       }
    }
    reloadLineChartData(any:EchartsLineOption){
       if(!any){
          this.initLineChartData();
       }else{
           this.LineEchartsOption = any;
       }

    }

    buildQueryObject(){
        this.ilRegionQuery = this.ilRegionQuery.Caliber(this.caliber);
        this.ilRegionQuery = this.ilRegionQuery.DeptNo(this.deptNo);
    }

    initBarChartData(){
        this.showLoadingByInstance();
        this.buildQueryObject();
        if(this.currSubTab==1){
            this.service.queryLastMonthAreaList(this.ilRegionQuery).then(
                data => {
                    this.BarEchartsOption = this.service.buildLastMonthAreaBarOption(data.body,this.title);
                        if(this.caliber==0){
                            this.AreaSalesBarOutOption = this.BarEchartsOption;
                        }else{
                            this.AreaSalesBarOption = this.BarEchartsOption;
                        }
                        this.hideLoadingByInstance();
                }
            );
        }
        
        
        if(this.currSubTab==2){
            this.service.queryLastMonthWarehouseList(this.ilRegionQuery).then(
                data => {
                    this.BarEchartsOption = this.service.buildLastMonthAreaBarOption(data.body,this.title);
                    if(this.caliber==0){
                            this.WarehouseSalesBarOutOption = this.BarEchartsOption;
                        }else{
                            this.WarehouseSalesBarOption = this.BarEchartsOption;
                        }
                        this.hideLoadingByInstance();
                }
            );
        }
    }

    initLineChartData(){
        this.showLoadingByInstance();
        this.buildQueryObject();
        if(this.currSubTab==1){
            this.service.queryAllYearAreaList(this.ilRegionQuery).then(
                data => {
                     this.LineEchartsOption = this.service.buildAllYearLineOption(data.body);
                    if(this.LineEchartsOption.series.length==0){
                       this.LineEchartsOption.series=[{
                            name:'',data:['-'],type:'line'},
                            {name:'',data:['-'],type:'line'},
                           { name:'',data:['-'],type:'line'},
                           { name:'',data:['-'],type:'line'},
                           { name:'',data:['-'],type:'line'},
                           { name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'},
                          {  name:'',data:['-'],type:'line'}];
                    }
                        if(this.caliber==0){
                            this.AreaAllSalesOutLineOption = this.LineEchartsOption;
                            
                        }else{
                            this.AreaAllSalesLineOption = this.LineEchartsOption;
                        }
                        this.hideLoadingByInstance();
                }
            );
        }
        
        
        if(this.currSubTab==2){
            this.service.queryAllYearWarehouseList(this.ilRegionQuery).then(
                data => {
                    this.LineEchartsOption = this.service.buildAllYearLineOption(data.body);
                    if(this.LineEchartsOption.series.length==0){
                      this.LineEchartsOption.series=[{
                            name:'',data:['-'],type:'line'
                         }];
                    }
                    if(this.caliber==0){
                            this.WarehouseAllSalesOutLineOption = this.LineEchartsOption;
                        }else{
                            this.WarehouseAllSalesLineOption = this.LineEchartsOption;
                        }
                        this.hideLoadingByInstance();
                }
            );
        }
    }

    fillAreaSalesLineOption(warehouse:EchartsLineOption,area:EchartsLineOption){
       if(warehouse && area && warehouse.series.length>area.series.length){
         var count = warehouse.series.length-area.series.length;
         for (var i = 0; i <count; i++) {
             area.series.push({
                name:'',data:[],itemStyle:'',type:'line'
             });
         }
       }
    }

    fillWarehouseSalesLineOption(area:EchartsLineOption,warehouse:EchartsLineOption){
       if(warehouse && area && warehouse.series.length<area.series.length){
         var count = area.series.length-warehouse.series.length;
         for (var i = 0; i <count; i++) {
             warehouse.series.push({
                name:'',data:[],itemStyle:'',type:'line'
             });
         }
       }
    }


    onChartInit(ec) {
        this.chartInstance = ec;
    }
    onLineInit(ec) {
        this.lineInstance = ec;
    }

    showLoadingByInstance() {
    if (this.chartInstance) {
      this.chartInstance.showLoading();
     }
    }

    hideLoadingByInstance() {
    if (this.chartInstance) {
      this.chartInstance.hideLoading();
     }
    }
 
  
    ngOnInit() {

    

    }
}



