import {Component, OnInit, ViewChild} from '@angular/core';
import {routerTransition} from '../../router.animations';
import { PartSaleDistComponent } from './part-sale-dist/part-sale-dist.component';
import { PartOrderDistComponent } from './part-order-dist/part-order-dist.component';
import { AreaOrderDataComponent } from './area-order-data/area-order-data.component';
import { AreaSaleDataComponent } from './area-sale-data/area-sale-data.component';
import { PartitionAnalysisService } from './service/partition-analysis.service';
import { TimeEstimateAnalysisComponent } from './time-estimate-analysis/time-estimate-analysis.component';
import { SaleDistAnalysisComponent } from './sale-dist-analysis/sale-dist-analysis.component';
import { NzNotificationService } from 'ng-zorro-antd';
@Component({
  selector: 'app-partition-analysis',
  templateUrl: './partition-analysis.component.html',
  styleUrls: ['./partition-analysis.component.scss'],
  animations: [routerTransition()]
})
export class PartitionAnalysisComponent implements OnInit {
    title = '';
    constructor(private service: PartitionAnalysisService,
        private _notification: NzNotificationService) { }
    _position = 1;
    _tabIndex = 0;
    _size = 1; //
    _selectedOptionValue; //
    options;
    @ViewChild(PartSaleDistComponent)
    PartSaleDist: PartSaleDistComponent;

    @ViewChild(PartOrderDistComponent)
    PartOrderDist: PartOrderDistComponent;

    @ViewChild(AreaOrderDataComponent)
    AreaOrderData: AreaOrderDataComponent;

    @ViewChild(AreaSaleDataComponent)
    AreaSaleData: AreaSaleDataComponent;

    @ViewChild(TimeEstimateAnalysisComponent)
    TimeEstimateAnalysis: TimeEstimateAnalysisComponent;

    @ViewChild(SaleDistAnalysisComponent)
    SaleDistAnalysis: SaleDistAnalysisComponent;
    changeDataCaliber(){
        this.PartSaleDist.caliber = +this._position;
        this.PartSaleDist.onSelectTabChange();

        this.PartOrderDist.caliber = +this._position;
        this.PartOrderDist.onSelectTabChange();

        this.AreaOrderData.caliber = this._position;
        this.AreaOrderData.changeOrdTableData();

        this.AreaSaleData.caliber = this._position;
        this.AreaSaleData.changeSalesTableData();
    }

    OnSelectSubChange(){
        if(this._tabIndex==0){
            this.PartSaleDist.currSubTab = +this._size;
            //this.PartSaleDist.LineEchartsOption.
            //this.PartSaleDist.lineInstance.clear();
           // this.PartSaleDist.lineInstance.restore();
            this.PartSaleDist.onSelectTabChange();
        }else if(this._tabIndex==1){
            this.PartOrderDist.currSubTab = +this._size;
            //this.PartOrderDist.lineChartInstance.clear();
            //this.PartSaleDist.lineInstance.setOption({});
           // this.PartOrderDist.lineChartInstance.restore();
            this.PartOrderDist.onSelectTabChange();
        }
    }

 /**
   *
   * @param 
   */
  readDeptList() {
    this.service.deptslist().then(result => {
      if (result.success && result.body) {
         
          this.options = result.body;
          this._selectedOptionValue = this.options[0].value;
          this.onDeptChange();
      } else {
        this._notification.error("", result.message);
      }
    }, err => {
      this._notification.error("", "");
    });
  }

  onDeptChange(){
      this.SaleDistAnalysis.deptNo = this._selectedOptionValue;
      this.SaleDistAnalysis.salesOption = null;
      this.SaleDistAnalysis.initRegionSalesData();

      this.TimeEstimateAnalysis.deptNo = this._selectedOptionValue;
      this.TimeEstimateAnalysis.timeOption = null;
      this.TimeEstimateAnalysis.initLastMonthRegionTime();

      this.PartSaleDist.deptNo = this._selectedOptionValue;
      this._size = 1;
      this.PartSaleDist.currSubTab = 1;
      this.PartSaleDist.setPartSaleDistOptionToNull();
      this.PartSaleDist.onSelectTabChange();

      this.PartOrderDist.deptNo = this._selectedOptionValue;
      this.PartOrderDist.currSubTab = 1;
      this.PartOrderDist.setPartOrderDistOptionToNull();
      this.PartOrderDist.onSelectTabChange();

      this.AreaOrderData.deptNo = this._selectedOptionValue;
      this.AreaOrderData.setAreaOrderDataToNull()
      this.AreaOrderData.changeOrdTableData();

      this.AreaSaleData.deptNo = this._selectedOptionValue;
      this.AreaSaleData.setAreaSaleDataToNull();
      this.AreaSaleData.changeSalesTableData();
  }
  OnTabChange(){
   // if(this._tabIndex == 0 || this._tabIndex == 1){
        this._size = 1;
        this.OnSelectSubChange();
    //}
      
  }


    ngOnInit() {
        this.readDeptList();
  }

}



