import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MdTabsModule } from '@angular/material';
import { FormsModule } from '@angular/forms';

import { YscComponentModule } from 'ysc-pop-component';
import { AreaOrderDataComponent } from './area-order-data/area-order-data.component';
import { AreaSaleDataComponent } from './area-sale-data/area-sale-data.component';
import { PartOrderDistComponent } from './part-order-dist/part-order-dist.component';
import { PartSaleDistComponent } from './part-sale-dist/part-sale-dist.component';
import { PartitionAnalysisComponent } from './partition-analysis.component';
import { PartitionAnalysisRoutingModule } from './partition-analysis.routing.module';
import { SaleDistAnalysisComponent } from './sale-dist-analysis/sale-dist-analysis.component';
import { PartitionAnalysisService } from './service/partition-analysis.service';
import { NgZorroAntdModule} from "ng-zorro-antd";
import { NgxDatatableModule }   from "@swimlane/ngx-datatable";
import { AngularEchartsModule } from 'ngx-echarts';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { TimeEstimateAnalysisComponent } from './time-estimate-analysis/time-estimate-analysis.component';
@NgModule({
  imports: [
      FormsModule,
    CommonModule,
    YscComponentModule,
  	PartitionAnalysisRoutingModule,
    MdTabsModule,
    NgbModule.forRoot(),
    NgZorroAntdModule.forRoot(),
      NgxDatatableModule,
      AngularEchartsModule
  ],
  declarations: [AreaOrderDataComponent,
                 AreaSaleDataComponent,
                 PartOrderDistComponent, 
                 PartSaleDistComponent, 
                 PartitionAnalysisComponent, 
                 SaleDistAnalysisComponent, TimeEstimateAnalysisComponent ],
  providers:[PartitionAnalysisService]
})
export class PartitionAnalysisModule { }



