import { Component, OnInit } from '@angular/core';
import { PartitionAnalysisService } from '../service/partition-analysis.service';
import { EchartsBarOption } from '../model/il-region-chart.option';
import echarts from 'echarts/dist/echarts.js';
@Component({
  selector: 'app-sale-dist-analysis',
  templateUrl: './sale-dist-analysis.component.html',
  styleUrls: ['./sale-dist-analysis.component.scss']
})
export class SaleDistAnalysisComponent implements OnInit {

  constructor(private service: PartitionAnalysisService) { }

  salesOption:EchartsBarOption;
  description1:string = '';
  description2:string = '';
  deptNo:string;

  initRegionSalesData(){
      if(this.deptNo){
            this.service.queryLastMonthCaliberOfRegionSales(this.deptNo).then(
            data => {
                this.salesOption = this.service.buildLastMonthSalesAnalysisBarOption(data.body);
                let value =  data.body.description;
                let index = value.lastIndexOf('@');
                    if(index && index<value.length){
                      this.description1 = value.substr(0,index);
                       this.description2 = value = value.substr(index+1,value.length);
                    }else{
                        this.description1 = value;
                    } 

                    this.salesOption.series[0].itemStyle.normal.color= new echarts.graphic.LinearGradient(0,0,0,1,[{
                            offset: 0, color: 'rgba(122,198,255,1)'
                          },{
                            offset: 1, color: 'rgba(122,198,255,0.5)'
                          }],false);
                    this.salesOption.series[1].itemStyle.normal.color= new echarts.graphic.LinearGradient(0,0,0,1,[{
                            offset: 0, color: 'rgba(159,217,107,1)'
                          },{
                            offset: 1, color: 'rgba(159,217,107,0.5)'
                          }],false);
                    this.salesOption.yAxis[0].name='';
                }
                
            );
      }else{
          this.salesOption = new EchartsBarOption;
      }
      
  }

  ngOnInit() {
  }

}



