import { Component, OnInit } from '@angular/core';
import { PartitionAnalysisService } from '../service/partition-analysis.service';
import { EchartsBarOption } from '../model/il-region-chart.option';
import { NzNotificationService } from 'ng-zorro-antd';
import echarts from 'echarts/dist/echarts.js';
@Component({
  selector: 'app-time-estimate-analysis',
  templateUrl: './time-estimate-analysis.component.html',
  styleUrls: ['./time-estimate-analysis.component.scss']
})
export class TimeEstimateAnalysisComponent implements OnInit {

   constructor(private service: PartitionAnalysisService,
       private _notification: NzNotificationService) { }

   

  deptNo:string;

  timeOption:EchartsBarOption;

  description:string;

  initLastMonthRegionTime(){
      if(this.deptNo){
          this.service.queryLastMonthRegionTime(this.deptNo).then(
                  result => {
              if (result.success && result.body) {
                  this.timeOption = this.service.buildLastMonthTimeAnalysisBarOption(result.body);
                  this.description = result.body.description;
                  this.timeOption.series[0].markPoint.itemStyle={
                            normal:{
                                color:'red'
                            }}
                  this.timeOption.series[0].itemStyle.normal.color=new echarts.graphic.LinearGradient(0,0,0,1,[{
                            offset: 0, color: 'rgba(122,198,255,1)'
                          },{
                            offset: 1, color: 'rgba(122,198,255,0.5)'
                          }],false);
                  

              } else {
                this._notification.error("", result.message);
              }
            }, err => {
              this._notification.error("", "");
            }
           );
      }
  }

  ngOnInit() {  
  }
}



